#about {
    position: relative;
    width: 100%;
    min-height: 100vh;
    padding-top: 70px;
    padding-bottom: 100px;
    color: rgb(200, 200, 200);
    overflow: hidden;
    .me-container {
        position: relative;
        padding: 0 20px;
        margin: 0 auto;
        width: 100%;
        max-width: 600px;
        text-align: center;
        img {
            float: left;
            width: 50%;
            height: 100%;
            border-top: 1px solid white;
            border-bottom: 1px solid white;
            border-left: 1px solid white;
            background-color: rgba(255, 255, 255, 0.04);
            border-radius: 50%;
            margin: 0px 5px 10px -10px;
            animation: show-img 2s ease-in-out;
        }
        @keyframes show-img {
            0%,
            30% {
                opacity: 0;
                transform: translateX(-100vw);
            }
            60% {
                opacity: 0;
            }
            100% {
                opacity: 1;
                transform: translateX(0);
            }
        }
        #p1 {
            animation: p1 2.5s ease-in-out;
        }
        @keyframes p1 {
            0%,
            80% {
                opacity: 0;
                transform: translateX(100vw);
            }
            90% {
                transform: translateX(-5vw);
            }
            100% {
                opacity: 1;
                transform: translateX(0);
            }
        }
        #p2 {
            animation: p2 3s ease-in-out;
        }
        @keyframes p2 {
            0%,
            80% {
                opacity: 0;
                transform: translateX(-100vw);
            }
            90% {
                transform: translateX(5vw);
            }
            100% {
                opacity: 1;
                transform: translateX(0);
            }
        }
    }
    animation: bg 1.2s ease-in-out;
}