#portfolio {
    position: relative;
    width: 100%;
    min-height: 100vh;
    padding-top: 70px;
    padding-bottom: 100px;
    color: rgb(200, 200, 200);
    text-align: center;
    overflow: hidden;
    #work-gallery {
        width: 100%;
        display: flex;
        flex-wrap: wrap;
        position: relative;
    }
    .work {
        width: (100% / 3);
        height: (100% / 3);
        display: none;
        img {
            width: 100%;
        }
    }
    .show {
        display: block !important;
        animation: show 0.8s ease-in-out;
    }
    @keyframes show {
        0% {
            opacity: 0;
            transform: scale(0);
        }
        70% {
            transform: scale(1.2);
        }
        100% {
            opacity: 1;
            transform: scale(1);
        }
    }
}